.vertical-menu {
  width: 40px;
  background: #fff;
  border-radius: 0;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 8px 0;
  position: fixed;
  top: 130px; /* 탭바(38px) + 메뉴바(48px) + 헤더(44px) = 130px */
  bottom: 0;
  z-index: 200; // 모달보다 낮게 조정 (기존 10001 → 200)
  height: calc(100vh - 130px);
}

.vertical-menu.left {
  left: 0;
}

.vertical-menu.right {
  right: 0;
}

.vertical-menu button {
  width: 40px;
  height: 40px;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #555;
  border-radius: 0;
  transition: background 0.15s;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: auto;
  position: relative;
  z-index: 201; // vertical-menu보다 약간 높게 설정
}

.vertical-menu button:hover {
  background: #f0f0f0;
}

button .ai-icon-bg,
button img[src$="btn-rnb-ai-on@3x.png"] {
  background: #555;
  border-radius: 6px;
  padding: 2px;
  /* 필요시 width/height 조정 */
}

.vertical-menu img[src$="ico-page@3x.png"] {
  background: #555;     // 진한 회색 배경
  border-radius: 2px;   // 둥근 모서리
  padding: 2px;         // 패딩 추가
}

.zoom-vertical-group {
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
  margin-bottom: 90px;
}

.zoom-btn {
  width: 40px;
  height: 40px;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #555;
  border-radius: 0;
  transition: background 0.15s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.zoom-btn:hover {
  background: #f0f0f0;
}

.page-toggle-btn.active {
  background: #f0f0f0 !important;
  border-radius: 10px !important;
  transition: background 0.2s;
  width: 35px;
  height: 35px;
}

.page-toggle-btn.active svg rect {
  stroke: #fff !important;
  fill: #d32f2f !important;
  transition: stroke 0.2s, fill 0.2s;
}

.edit-toggle-btn.active {
  background: rgba(211, 47, 47, 0.13) !important;
  border-radius: 10px !important;
  transition: background 0.2s;
  width: 35px;
  height: 35px;
}

button[title*="번역"].active,
button[title*="AI 번역"].active {
  background: rgba(211, 47, 47, 0.13) !important;
  border-radius: 10px !important;
  transition: background 0.2s;
  width: 35px;
  height: 35px;
}

button[title*="AI 기능"].active {
  background: rgba(211, 47, 47, 0.13) !important;
  border-radius: 10px !important;
  transition: background 0.2s;
  width: 35px;
  height: 35px;
}

.pdf-content-btn.active {
  background: rgba(211, 47, 47, 0.13) !important;
  border-radius: 10px !important;
  transition: background 0.2s;
  width: 35px;
  height: 35px;
}
